;(function (window, $, undefined) {

	'use strict'

	if(window.location && window.location.hash.indexOf('#')>=0){
		window.location.href="/"
	}

	$(document).ready(function(){

		APP.stateman = new StateMan();
		APP.stateman.state({

		  "home": new HomeState(),
		  "play": new PlayState(),
		  "play.catch": new CatchState(),
		  "play.ask": new PlayAskState(),
		  "over": new OverState(),
		  "win": new WinState()

		}).on("notfound", function(){

			setTimeout(function(){
				APP.stateman.go("home")
			}, 250)

		  
		}).start({html5:false});


		APP.game.config( APP.back.elements )

	})


})(window, window.jQuery);


