function PlayState(){
    
	APP.bar.hide()

	return {
		enter: function(option){ console.log('PlayState :: enter');

			APP.game.start()

			APP.bar.show()
			APP.bar.setTarget( APP.game.getCurrent() )

			APP.panel.setButton('#play')

			APP.back.begin( APP.game.getCurrent() )

			APP.game.onTick(function(v, p){
	        	APP.bar.setTime(v, p)
		    })


		    APP.game.onOver(function(){
		    	APP.stateman.go('over')
		    })


		    APP.back.onClicked(function(){
		    	console.log('clicked')

		    	if(APP.game.over) return // enforcing concurrency

		    	APP.game.addItem()
		    	APP.bar.setBarIcon(1)
		    	APP.panel.setTarget( APP.game.getCurrent() )
		    	APP.back.pick( APP.game.getCurrent() )

		    	var vals = APP.game.getCurrentVal()
		    	APP.bar.setTime(vals.v, vals.p)

		    	APP.stateman.go('play.catch')

		    	
		    	if(APP.game.getLength() == 0){
		    		APP.game.abort()
			    	APP.bar.setTime(0,0)
			    	APP.panel.setGameOver()
			    	APP.panel.setTitle('You win!')
			    	APP.panel.setItemsNum( APP.game.getItems() )
		    	}
		    	
		    })


		},
		leave: function(option){ console.log('PlayState :: leave')

			APP.game.abort()

		}
	}

}
