;(function(window, $, undefined){

	APP.panel = {}

	$(document).ready(function(){

		var cont = $('#panel')
		var title = cont.find('[js_title]')
		var action = cont.find('[js_action]')
		var btn = cont.find('#btnPlay')
		var itms = cont.find('[js_items]')
		var itms_best = cont.find('[js_items_best]')


		function show(delay){
			var d = (delay) ? delay : 0
			//cont.show()
			cont.transition({y:0, delay:d}, 1500, 'easeInOutQuint')
		}

		function hide(){
			//cont.hide()
			cont.transition({y:300}, 0, 'easeInOutQuint')
		}

		function setTarget(id){
			var targets = cont.find('.target svg > g')
			targets.hide()

			var itm = targets.get(id)
			$(itm).show()
		}

		function setTitle(v){
			title.text(v)
		}

		function setAction(v){
			action.text(v)
		}

		function setButton(url){
			btn.attr('href', url)
		}

		function setGameOver(){
			cont.find('.gamein').hide()
			cont.find('.gameover').show()
		}

		function unsetGameOver(){
			cont.find('.gamein').show()
			cont.find('.gameover').hide()
		}

		function setItemsNum(v){
			itms.text(v)
		}



		APP.panel.setItemsNum = setItemsNum
		APP.panel.setGameOver = setGameOver
		APP.panel.unsetGameOver = unsetGameOver
		APP.panel.setButton = setButton
		APP.panel.setAction = setAction
		APP.panel.setTitle = setTitle
		APP.panel.setTarget = setTarget
		APP.panel.show = show
		APP.panel.hide = hide
	})



})(window, jQuery)