;(function(window, $, undefined){

	APP.back = {}

	$(document).ready(function(){

		var cont = $('#back')
		var svg = $('#back svg')

		var onClicked
		var disabled = false

		var inTransit = false
		var elements = svg.find('#uomini > g')

		elements.on('click', function(e){
			onClicked()
	    })

        var width_view = $(window).width()
        var width_content = $('#back').width()

        console.log(width_view, width_content)

        new Impetus({
             source:'#back',
             boundX:[(width_content-width_view)*-1, 0],
             bounce:false,
             update: function (x, y) {
                if(!inTransit && !disabled) $('#back').css({x:x})
             }
        })


        function restore(){
        	var width_view = $(window).width()
        	var width_content = $('#back').width()
			cont.css({x:(width_content-width_view)/2*-1})
			disabled=false
        }


        function begin(picked){

        	elements.css('display', 'none')
			$(elements.get(picked)).css('display', 'block')
			disabled=false

        	inTransit=true
        	cont.transition({x:0, delay:100, complete:function(){
        		inTransit=false
        	}}, 1500, 'easeInOutQuint')
        }


        function pick(picked){
        	elements.css('display', 'none')
			$(elements.get(picked)).css('display', 'block')
        }



		function show(){
			cont.show()
		}

		function hide(){
			cont.hide()
		}


		APP.back.onClicked = function(clb){
			onClicked = clb
		}

		APP.back.disable = function(clb){
			disabled = true
		}


		APP.back.elements = elements
		APP.back.restore = restore
		APP.back.begin = begin
		APP.back.pick = pick
		APP.back.hide = hide
		APP.back.show = show
	})



})(window, jQuery)