function HomeState(){


	return {
		enter: function(option){ console.log('HomeState :: enter');

			APP.game.reset()

			APP.home.show()

			APP.panel.show()
			APP.panel.setButton('#play')
			APP.panel.setTarget( APP.game.getCurrent() )
			APP.panel.unsetGameOver()

			var name = APP.game.getCurrentName()

			var dato = null
			APP.db.forEach(function(d){
				if(d.layer_name == name) dato = d
			})
			console.log(dato)

			APP.panel.setAction('Your first target')
			APP.panel.setTitle(dato.introduction_text)

			APP.bar.hide()
			var vals = APP.game.getCurrentVal()
		    APP.bar.setTime(vals.v, vals.p)

			APP.back.restore()
			
		},
		leave: function(option){ console.log('HomeState :: leave')
			
			APP.panel.hide()
			APP.home.hide()

		}
	}

}
