;(function(window, $, undefined){

	APP.bar = {}

	$(document).ready(function(){

		var cont = $('#bar')
		var val = cont.find('.timeleft span')
		var bar = cont.find('.time .time_bar')

		var scaleCol1 = d3.scaleLinear()
			.domain([0, 100])
			.range(['#E41D19', '#59BC60'])

		var scaleCol2 = d3.scaleLinear()
			.domain([0, 100])
			.range(['#FB5724', '#4CBAC2'])

		cont.find('[js_abort]').on('click', function(){
			
			//APP.stateman.go('play.ask')

			if (confirm('Abort?')) {
			    APP.stateman.go('home')
			}
			return false
		})

		var icon = cont.find('.icon_wrapper .icon')
		var done = cont.find('.icon_wrapper .done')
		var fail = cont.find('.icon_wrapper .fail')

		function show(){
			cont.show()
		}

		function hide(){
			cont.hide()
		}

		function setTime(v, p){
			val.text(v)
			val.css('color', scaleCol1(p))
	    	bar.css('width', p+'%')

	    	bar.css('background', 'linear-gradient(90deg, '+scaleCol2(p)+', '+scaleCol1(p)+')')
		}

		function setTarget(id){
			var targets = icon.find('svg > g')
			targets.hide()

			var itm = targets.get(id)
			$(itm).show()
			
			setBarIcon(0)
		}

		function setBarIcon(status){
	    	icon.hide()
	    	done.hide()
	    	fail.hide()

	    	if(status == 0) icon.show()
	    	if(status == 1) done.show()
	    	if(status == 2) fail.show()
	    }



		APP.bar.setBarIcon = setBarIcon
		APP.bar.setTarget = setTarget
		APP.bar.setTime = setTime
		APP.bar.hide = hide
		APP.bar.show = show
	})



})(window, jQuery)