;(function(window, $, undefined){

	APP.catch = {}

	$(document).ready(function(){

		var cont = $('#catch')
		var title = cont.find('h1')
		var desc = cont.find('p')

		function show(){
			cont.show()
		}

		function hide(){
			cont.hide()
		}

		function setTitle(t){
			title.text(t)
		}

		function setText(t){
			desc.text(t)
		}


		APP.catch.setTitle = setTitle
		APP.catch.setText = setText
		APP.catch.hide = hide
		APP.catch.show = show
	})



})(window, jQuery)